<template>

  <form class="search-form py-3 rounded-0 form-inline" @submit.prevent="searchGame">
    <div class="search-form__inner dark-background input-group rounded-0">
      <input v-model="searchParam" type="text" class="transparent-input form-control  rounded-0 search-game search-form__text"
              placeholder="Search Game">
      <button class="transparent-input btn btn-outline-secondary rounded-0 search-form__submit" type="submit">
        <i class="fa fa-search"></i>
      </button>
    </div>
  </form>

</template>

<script>
export default {
  name: 'SearchForm',
    data () { return { searchParam: '' }
  },
  methods: {
    searchGame () { this.$router.push({ name: 'search', params: { query: this.searchParam} }) }
  }
}
</script>

